.popup {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;


  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
  top: 50px;
  z-index: 150;
  left: 50%;
  margin-left: -160px;
  text-align: center;
}

.popup--failure {
  display: none;
  width: 320px;
  height: 280px;
  padding: 35px 0;
}

.popup--success {
  display: none;
  width: 320px;
  height: 323px;
  padding-top: 50px;

  & .popup__button {
    width: 280px;
  }
}

.popup__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
}

.popup__text {
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  width: 214px;
  margin: 0;
  .alignment();
  margin-bottom: 30px;
}

.popup__wrapper {
  box-sizing: border-box;
  width: 100%;
  background-color: #f2f2f2;
  padding: 20px;
}

.popup__button {
  width: 180px;
  .alignment();
}

.popup-show {
  display: flex;
}

.popup-close {
  display: none;
}

@media (min-width: @tablet-width) {
  .popup--failure {
    width: 400px;
    height: 323px;
    margin-left: -200px;

    & .popup__button {
      width: 270px;
    }

    & .popup__title {
      width: 240px;
    }

    & .popup__text {
      width: 280px;
    }
  }

  .popup--success {
    width: 640px;
    height: 347px;
    margin-left: -320px;

    & .popup__title {
      width: 410px;
    }

    & .popup__text {
      width: 420px;
    }

    & .popup__button {
      width: 460px;
    }
  }

  .popup__title {
    font-size: 30px;
    line-height: 40px;
  }

  .popup__text {
    margin: 0;
    .alignment();
    margin-bottom: 30px;
  }

  .popup__wrapper {
    padding: 40px;
  }

}
