.features__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.features__item {
  text-align: center;
}

.features__info {
  background-color: @grey;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  padding: 45px 30px;
}

.features__info--first {
  background-color: @blue;
  color: #ffffff;
}

.features__item:first-child .features__info {
  position: relative;
  padding-top: 61px;

  &::before {
    position: absolute;
    content: " ";
    height: 37px;
    width: 100%;
    top: 0;
    left: 0;

    background: url("../img/white copy 2.svg") no-repeat 50% 0;
    background-color: @blue;
  }
}

.features__title {
  font-size: 21px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 21px;
  /*border-top: 1px solid #d6d6d6;*/
  box-shadow:
  0 -39px 0 0 @grey,
  0 -40px 0 0 #d6d6d6;
}

.features__title--first,
.features__title--third {
  border: none;
  box-shadow: none;
}

.features__number {
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
}

.features__desc {
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  margin: 0;
  margin-top: 21px;
}

.features__image {
  width: 100%;
  height: auto;
  margin-bottom: -5px;
}

.features__pic {
  background-color: #cb735d;
  width: 100%;
}

@media (min-width: @tablet-width) {
  .features__list {
    display: flex;
    flex-wrap: wrap;
  }

  .features__item {
    width: 33.3%;
    background-color: @grey;
  }

  .features__item--first {
    min-width: 768px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    background-color: inherit;
  }

  .features__info {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0;
    padding: 20%;
  }

  .features__info--first {
    width: 33.3%;
    padding: 45px;
    justify-content: center;
  }

  .features__title {
    box-shadow: none;
  }

  .features__image {
    width: 66.6%;
    box-sizing: border-box;
  }

  .features__item:first-child .features__info {
    padding-top: 45px;

    &::before {
      display: none;
    }
  }
}
