.main-header {
  text-align: center;
  max-width: 490px;
  min-width: 280px;
  box-sizing: border-box;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 43px;
  .alignment();
  margin-bottom: 30px;
}

.main-header__title {
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 40px;
}

.main-header__desc {
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
}

@media (min-width: @tablet-width) {
  .main-header {
    max-width: 480px;
    margin-bottom: 75px;
    padding-top: 75px;
  }

  .main-header__title {
    font-size: 30px;
  }

  .main-header--index {
    max-width: 540px;
  }
}
