.site-button {
  display: block;
  border: none;
  color: #ffffff;
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: @blue;
  padding-top: 17px;
  padding-bottom: 17px;

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
      color: #87b5d1;
    }
}
