.page-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.page-header__logo {
  align-self: center;
  margin-bottom: -27px;
  z-index: 2;
}

.page-header__main {
  position: relative;
  background-color: #e4f1fa;
  background-image: url("../img/back-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 768px auto;
  background-position: 50% -221px;
  padding-top: 50px;
  text-align: center;
  padding-bottom: 65px;

  &::after {
    position: absolute;
    content: "";
    min-height: 56px;
    height: auto;
    width: 100%;
    bottom: 0;
    left: 0;

    background: url("../img/whiteback-top.svg") no-repeat 50% 0;
    background-size: 1204px auto;
  }
}

.page-header__main--index {
  background-position: 50% 0;
  padding-top: 90px;
  padding-bottom: 75px;
}

@media (min-width: @tablet-width) {
  .page-header__logo {
    margin-top: -66px;
  }

  .page-header__main {
    background-image: url("../img/back-desktop.jpg");
    background-size: 1200px auto;
    padding-top: 90px;
    padding-bottom: 120px;
  }

  .page-header__main--index {
    padding-bottom: 70px;
    padding-top: 70px;
  }

  .page-header__image {
    width: 458px;
    height: 78px;
  }

  .page-header__image--index {
    width: 460px;
    height: 350px;
  }
}

@media (min-width: @desktop-width) {
  .page-header__main {
    background-size: 100% auto;


    &::after {
      background-size: 100% auto;
    }
  }

  .page-header__logo-image {
    width: 188px;
    height: 96px;
  }

  .page-header__logo {
    margin-bottom: -40px;
  }
}
