.benefits {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: left;
  max-width: 520px;
  .alignment();
}

.benefits__item {
  padding: 15px 20px 20px 0;
  text-transform: uppercase;
  margin-left: 20px;
  margin-right: 20px;
}

.benefits__item--motel::before {
  background: url('../img/icon-burger.svg')
}

.benefits__icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}

.benefits__icon-pic {
  fill: #81b3d2;
}

.benefits__title {
  display: inline-block;
  vertical-align: middle;
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  margin: 0;
}

.benefits__desc {
  padding-left: 50px;
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  margin-top: 30px;
}

@media (min-width: @tablet-width) {
  .benefits {
    display: flex;
    justify-content: space-around;
    max-width: none;
    margin-bottom: 40px;
    margin-top: 60px;
  }

  .benefits__item {
    padding: 20px;
    text-align: center;
    margin: 0;
  }

  .benefits__desc {
    padding-left: 0;
  }

  .benefits__icon {
    display: block;
    margin-bottom: 30px;

    &--motel svg {
      width: 75px;
      height: 72px;
    }

    &--burger svg {
      width: 77px;
      height: 68px;
    }

    &--souvenirs svg {
      width: 64px;
      height: 75px;
    }
  }
}
