@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/pt-sans-regular.woff2"), url("../fonts/pt-sans-regular.woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/pt-sans-700.woff2"), url("../fonts/pt-sans-700.woff");
}
/*colors*/
body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #333333;
  font-size: 21px;
  line-height: 24px;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 280px;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 0;
  margin-left: auto;
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1220px;
  }
}
.page-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.page-header__logo {
  align-self: center;
  margin-bottom: -27px;
  z-index: 2;
}
.page-header__main {
  position: relative;
  background-color: #e4f1fa;
  background-image: url("../img/back-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 768px auto;
  background-position: 50% -221px;
  padding-top: 50px;
  text-align: center;
  padding-bottom: 65px;
}
.page-header__main::after {
  position: absolute;
  content: "";
  min-height: 56px;
  height: auto;
