.main-nav__list {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__item {
  background-color: #81b3d2;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.main-nav__item:hover {
  background-color: #669ec0;
}
.main-nav__item:active {
  background-color: #5496bd;
}
.main-nav__item:active a {
  color: #87b5d1;
}
.main-nav__link {
  display: block;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 40px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  padding-top: 8px;
  padding-bottom: 8px;
}
.main-nav__toggle {
  position: absolute;
  width: 23px;
  height: 21px;
  border: none;
  right: 20px;
  top: 17px;
  padding: 0;
  z-index: 2;
  background-color: transparent;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 24px;
  height: 3px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
.main-nav--opened .main-nav__toggle {
  right: 20px;
  top: 17px;
  height: 23px;
  width: 23px;
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 9px;
  left: -3px;
  width: 29px;
  height: 4px;
  transform: rotate(45deg);
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  right: -3px;
  width: 29px;
  height: 4px;
  transform: rotate(-45deg);
  background-color: #ffffff;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: flex;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    padding-left: 49px;
    padding-right: 26px;
    display: flex;
    justify-content: space-between;
  }
  .main-nav__list {
    position: static;
    flex-direction: row;
    justify-content: space-between;
    width: 240px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__item {
    background-color: white;
    box-shadow: none;
  }
  .main-nav__item:hover {
    background-color: white;
  }
  .main-nav__item:hover a {
    color: #81b3d2;
  }
  .main-nav__item:active a {
