.social {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
  width: 151px;
}

.social__item {
  display: block;
  box-sizing: border-box;
  width: 46px;
  height: 48px;
  background-color: #81b3d2;
  vertical-align: middle;

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;

    & svg {
      fill: #87b5d1;
    }
  }
}

.social__link {
  display: block;
  box-sizing: border-box;
  padding-top: 12px;
}
