.feedback__button {
  width: 280px;
  margin: 50px auto;
}

.feedback__wrapper p {
  display: none;
}


@media (min-width: @tablet-width) {
  .feedback {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 50px;
    padding-right: 50px;
  }

  .feedback__fieldset.fieldset--impression {
    order: 1;
    width: 50%;
  }

  .feedback__fieldset.fieldset--name {
    order: 2;
    width: 50%;
  }

  .feedback__fieldset.fieldset--contacts {
    order: 3;
    width: 100%;
  }

  .feedback__fieldset.fieldset--landmarks {
    order: 4;
    width: 100%;
  }

  .feedback__fieldset.fieldset--textarea {
    order: 5;
    width: 100%;
  }

  .feedback__wrapper {
    order: 6;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
  }

  .feedback__wrapper p {
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    text-transform: uppercase;
  }

  .feedback__button {
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .feedback__button {
    width: 50%;
  }
}
