.fieldset {
  border: none;
  padding: 20px;
  text-align: center;
  box-sizing: border-box;
}

.fieldset:not(:last-of-type) {
  border-bottom: 1px solid #e5e5e5;
}

.fieldset__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.fieldset__list--name {
  display: block;
}

.fieldset__legend {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  padding-top: 20px;
  z-index: 0;
}

.fieldset__item {
  text-align: left;
}

.fieldset__list--name .fieldset__item {
  display: flex;
  align-items: baseline;
  margin-bottom: 20px;
}


.fieldset__label {
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  margin-right: 10px;
}

.fieldset__label--impression,
.fieldset__label--landmarks {
  position: relative;
  display: block;
  margin: 0;
  padding-left: 75px;
  margin-bottom: 35px;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: none;

  &::before {
    position: absolute;
    content: "";
    width: 46px;
    height: 46px;
    border: 5px solid #f2f2f2;
    box-sizing: border-box;
    background-color: #f2f2f2;
    left: 5px;
    top: -10px;
  }

  &:hover::before {
    border: 5px solid #e5e5e5;
    background-color: #e5e5e5;
  }
}

.fieldset__label--impression {
  &::before{
    border-radius: 50%;
  }
}

.fieldset--impression input:checked~.fieldset__label--impression::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  border: 3px solid #333333;
  background-color: #333333;
  border-radius: 50%;
  left: 17px;
  top: 1px;
}

.fieldset--impression input:disabled~.fieldset__label{
  &::before {
    border: 5px solid #d5d5d5;
    background-color: #d5d5d5;
  }
  &::after {
    opacity: 0.3;
  }
}

.fieldset--landmarks input:disabled~.fieldset__label{
  &::before {
    border: 5px solid #d5d5d5;
    background-color: #d5d5d5;
  }
  &::after {
    opacity: 0.3;
  }
}

.fieldset--landmarks input:checked~.fieldset__label--landmarks::after {
  position: absolute;
  content: "";
  width: 24px;
  height: 16px;
  background: url("../img/tick.svg") no-repeat;
  left: 17px;
  top: 5px;
}


.fieldset--contacts .fieldset__label {
  margin-left: 20px;
}

.fieldset input[type="text"],
.fieldset input[type="tel"],
.fieldset input[type="email"],
.fieldset textarea {
  display: block;
  box-sizing: border-box;
  background-color: #f2f2f2;
  border: none;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  padding: 17px;
  width: 100%;
  min-width: 198px;

  &:hover {
    background-color: #e5e5e5;
  }

  &:focus {
    border: 4px solid #e5e5e5;
    background-color: #ffffff;
    outline: none;
    padding-top: 13px;
    padding-bottom: 13px;
  }
}

.fieldset input[type="tel"],
.fieldset input[type="email"] {
  padding-right: 60px;
}

.fieldset__list--impression input,
.fieldset--landmarks input {
  display: none;
}

.fieldset--contacts .fieldset__item {
  position: relative;
  box-sizing: border-box;

  &::after {
    position: absolute;
    content: "";
    display: block;
    width: 60px;
    height: 60px;
    background-color: #e6e6e6;
    top: 27px;
    right: 0;
  }
}

.fieldset__item--phone::after {
  background: url("../img/phone.svg") no-repeat center;
}

.fieldset__item--email::after {
  background: url("../img/mail.svg") no-repeat center;
}

.fieldset textarea {
  width: 100%;
}

@media (min-width: @tablet-width) {
  .fieldset__legend {
    padding: 0;
    text-align: left;
  }

  .fieldset {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    padding-top: 50px;
  }


  .fieldset:not(:last-of-type) {
    border-bottom: none;
  }

  .fieldset--name {
    border-left: 1px solid #e5e5e5;
    padding-left: 50px;
  }

  .fieldset--contacts {
    margin-top: 75px;
    border-top: 1px solid #e5e5e5;
    margin-bottom: 75px;

    & .fieldset__legend {
      padding: 0 60px;
      width: 380px;
      box-sizing: border-box;
      text-align: center;
    }

    & .fieldset__item {
      margin-bottom: 20px;
    }

    & .fieldset__label {
      margin-left: 17px;
    }
  }

  .fieldset--landmarks  {
    border-top: 1px solid #e5e5e5;
    margin-bottom: 75px;
    padding-top: 0;

    & .fieldset__legend {
      padding: 0 60px;
      width: 380px;
      box-sizing: border-box;
      text-align: center;
    }

    & .fieldset__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding-left: 70px;
      padding-right: 70px;
    }

    & .fieldset__item {
      width: 218px;
      padding-left: 10px;
    }
  }

  .fieldset--textarea {
    border-top: 1px solid #e5e5e5;
    margin-bottom: 50px;

    & .fieldset__legend {
      padding: 0 60px;
      width: 380px;
      box-sizing: border-box;
      text-align: center;
    }
  }

  .fieldset__label--landmarks {
    margin-bottom: 0;
    margin-top: 50px;
  }

  .fieldset--impression .fieldset__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 20px;
  }

  .fieldset__label--impression {
    margin-bottom: 52px;
  }

  .fieldset__item:last-of-type .fieldset__label--impression {
    margin-bottom: 0;
  }

  .fieldset__item:last-child {
    margin-bottom: 0;
  }

  .fieldset input[type="text"],
  .fieldset input[type="tel"],
  .fieldset input[type="email"],
  .fieldset textarea {
    min-width: 175px;
  }
}

@media (min-width: @desktop-width) {
  .fieldset__list--contacts {
    display: flex;
    justify-content: space-between;

    & .fieldset__item {
      display: flex;
      align-items: center;
      width: 48%;
      margin-bottom: 0;

      &::after {
        top: 0;
      }
    }

    & .fieldset__label {
      margin: 0;
      margin-right: 10px;
      white-space: nowrap;
    }
  }
}
