.booking {
  text-align: center;
}

.booking--photo {
  padding-top: 20px;
}

.booking__info {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 33px 20px 50px 20px;
}

.booking__question {
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 16px;
}

.booking__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  .alignment();
  margin-bottom: 35px;
  width: 100%;
  min-width: 280px;
  max-width: 540px;
}

.booking__button {
  .alignment();
  width: 100%;
  min-width: 280px;
  max-width: 540px;
}

.booking__map {
  background-color: #e4f1fa;
  width: 100%;
  max-width: 540px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.booking__video {
  width: 320px;
  height: 200px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: @tablet-width) {
  .booking {
    margin-bottom: 40px;
  }

  .booking__map {
    max-width: none;
  }

  .booking__question {
    font-size: 30px;
  }

  .booking__text {
    margin-bottom: 75px;
  }
}

@media (min-width: @desktop-width) {
  .booking {
    margin-bottom: 80px;
  }
}
