.footer {
  text-align: center;
  padding-top: 31px;
  padding-bottom: 25px;
}

.footer__hashtag {
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 40px;
}

.footer__social {
  .alignment();
  margin-bottom: 30px;
}

.footer__htmlac {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 151px;
  .alignment();
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;

  &:hover svg {
    fill: #81b3d2;
  }

  &:active svg {
    fill: #000000;
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 36px 60px;
  }

  .footer__hashtag {
    margin-bottom: 0;
  }

  .footer__social {
    margin: 0;
  }
}
