.album {
  text-align: center;
}

.album__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.album__item {
  padding: 20px;
  text-align: left;
  border-bottom: 1px solid #e5e5e5;
}

.album__item:first-child {
  border-top: 1px solid #e5e5e5;
}

.album__photo {
  width: 100%;
  margin-bottom: 20px;
  background-color: #aed5fc;
}

.album__title {
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

.album__author {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

.album__likes {
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;

  & svg {
    margin-right: 10px;
    vertical-align: bottom;
  }

  & span {
    vertical-align: bottom;
  }
}

@media (min-width: @tablet-width) {
.album {
  margin-bottom: 65px;
}

  .album__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: 60px;
    margin-right: 60px;
  }

  .album__item {
    padding: 0;
    margin: 0;
    width: 50%;
    border: none;
  }

  .album__item:first-child {
    width: 100%;
  }

  .album__desc {
    background-color: #f2f2f2;
    padding: 12px 20px;
  }

  .album__photo {
    margin-bottom: -5px;
  }
}

@media (min-width: @desktop-width) {
  .album__list {
    margin: 0;
  }

  .album__item {
    position: relative;
    width: 33.33%;
  }

  .album__photo {
    margin-bottom: -5px;
  }

  .album__desc {
    position: absolute;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    box-sizing: border-box;
    height: 80px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    color: white;
  }

  .album__likes {
    font-size: 11px;
    text-align: right;

    & path {
      fill: white;
    }
  }
}
